#nav-bar {
  position: sticky;
  top: 0;
  z-index: 10;
}

.navbar {
  background-color: #32be8f;
  padding: 8px !important;
}

.navbar-nav li {
  padding: 0 10px;
}

.navbar-nav li a {
  color: #fff !important;
  font-weight: 600;
  float: right;
  text-align: left;
  cursor: pointer;
}

.navbar-brand img {
  height: 50px;
  padding-left: 20px;
}

.fa-bars {
  color: #fff;
  font-size: 30px !important;
}

.navbar-toggler {
  outline: none !important;
}

.navbar .btn {
  box-shadow: none;
  float: right;
  padding: 8px 25px;
  border: none;
  border-radius: 20px;
}

#home {
  background-color: #32be8f;
  color: #fff;
  height: 82vh;
  clip-path: polygon(0 0, 100% 0, 100% 70%, 0 100%);
}

.home-content {
  margin-top: 150px !important;
}
